,0BACI,cAAA,CAEJ,2BACI,aAAA,CAAA","sourcesContent":["$dk-color : #222121;\r\n$pm-color : #CDF921;\r\n$light : #dafb56; \r\n$sc-color : #f86200;\r\n\r\nhtml,\r\nbody {\r\n    height: 100%;\r\n    margin: 0 auto;\r\n    padding: 0;\r\n    background-repeat: no-repeat;\r\n    background-color:$pm-color;\r\n    background-image: url('../assets/quiz-bg.svg');\r\n    color: $dk-color;\r\n    line-height: normal;\r\n    font-size: 1rem;\r\n    font-family: Montserrat, -apple-system, blinkmacsystemfont, segoe ui, roboto, helvetica neue, arial, noto sans, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol, noto color emoji;\r\n    scroll-behavior: smooth;\r\n}\r\n\r\nh1,h2,h3,h4,h5,h6,p,ol,ul,li,input{\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\nmain{\r\n    height: 100%;\r\n}\r\n#outlet{\r\n    height: 100%;\r\n}\r\n\r\na.link{\r\n    text-decoration: none;\r\n    display: block;\r\n    color: inherit;\r\n    cursor: pointer;\r\n    font-weight: 500;\r\n    padding: 5px 10px;\r\n    border-radius: 4px;\r\n    transition: all 0.2s ease-in-out;\r\n    &:hover{\r\n        color: $light;\r\n        background-color: $dk-color;\r\n        \r\n    }\r\n}\r\n\r\nbutton{\r\n    border: none;\r\n    background-color: unset;\r\n    font-size: inherit;\r\n    line-height: 2rem;\r\n    font-family: inherit;\r\n    color: inherit;\r\n    cursor: pointer;\r\n    &:hover{\r\n        filter: brightness(85%);\r\n    }\r\n}\r\n\r\n.d-flex-row{\r\n    display: flex;\r\n    flex-direction: row;\r\n    gap: 10px;\r\n}\r\n.justify-between{\r\n    justify-content: space-between;\r\n}\r\n.p-20{\r\n    padding: 10px 20px;\r\n}\r\n.logo{\r\n    letter-spacing: -2px;\r\n    border-radius: 3px;\r\n    color: $dk-color;\r\n    border-left: 4px solid;\r\n    padding: 1px 5px;\r\n    box-shadow: 2px 4px 20px rgb(0 0 0 / 12%);\r\n}\r\n.play::after{\r\n    content: \"\";\r\n    width: 10px;\r\n    height: 10px;\r\n    background: transparent;\r\n    position: absolute;\r\n    top: calc(50% - 5px);\r\n    box-shadow: 3px -3px 0px $pm-color;\r\n    transform: rotate(45deg);\r\n    right: 65%;\r\n}\r\n\r\n.start-btn{\r\n    background-color: $dk-color;\r\n    position: relative;\r\n    font-size: 20px;\r\n    text-align: center;\r\n    color: $pm-color !important;\r\n    letter-spacing: 2px;\r\n    padding: 15px !important;\r\n    margin: 5px 10px;\r\n    &:hover{\r\n        transform: scale(1.05);\r\n        box-shadow: 0px 10px 0px white;\r\n    }\r\n}\r\n\r\n// #outlet{\r\n//     height: 90vh;\r\n//     overflow: hidden;\r\n// }\r\n\r\nmain{\r\n    .banner{\r\n        padding: 10px 5%;\r\n        position: sticky;\r\n        backdrop-filter: blur(1px);\r\n        background: rgb(255 255 255 / 10%);\r\n        top: 0;\r\n        z-index: 100;\r\n        a{\r\n            margin-left: 20px;\r\n        }\r\n        &+div{\r\n            align-items: center;\r\n        }\r\n    }\r\n}\r\n\r\n.container{\r\n    padding: 10px 5%;\r\n    width: 90%;\r\n    max-height :calc(100% - 20px);\r\n    .home{\r\n        display: grid;\r\n        place-content: center;\r\n        .cat-rule{\r\n            display: flex;\r\n            flex-direction: row;\r\n            gap: 10px;\r\n            justify-content: space-between;\r\n        }\r\n        .cat-list{\r\n            backdrop-filter: blur(1px);\r\n            background: rgb(255 255 255 / 20%);\r\n            margin: 20px 0;\r\n            padding: 3%;\r\n            border-radius: 10px;\r\n            letter-spacing: -1px;\r\n            h2{\r\n                margin-left: 10px;\r\n            }\r\n            .list{\r\n                margin: 20px 0;\r\n                flex-wrap: wrap;\r\n                li{\r\n                    border-radius: 5px;\r\n                    font-size: 15px;\r\n                    font-weight: 500;\r\n                    letter-spacing: 1px;\r\n                    \r\n                    label > .cat-title{\r\n                        border: 2px dashed rgb(34 33 33 / 20%);\r\n                        padding: 20px;\r\n                        cursor: pointer;\r\n                        border-radius: 5px;\r\n                        transition: all 0.2s ease-in-out;\r\n                    }\r\n                    \r\n                    input[type=radio]:checked + label > .cat-title{\r\n                        color: white;\r\n                        background-color: $dk-color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        .rules{\r\n            margin: 20px 0;\r\n            padding: 3%;\r\n            backdrop-filter: blur(1px);\r\n            background: rgb(255 255 255 / 20%);\r\n            border-radius: 10px;\r\n            ul{\r\n                margin: 10px 0;\r\n                li{\r\n                    padding: 5px 0px;\r\n                    margin: 10px 0;\r\n                    code{\r\n                        color: $sc-color;\r\n                        white-space: nowrap;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n    h1.heading{\r\n        font-size: 100px;\r\n        font-weight: 800;\r\n        position: relative;\r\n        &::after{\r\n            content: \"\";\r\n            position: absolute;\r\n            width: 100%;\r\n            height: 15px;\r\n            background-color: rgb(255 199 0 / 19%);\r\n            bottom: 5px;\r\n            left: 0;\r\n            z-index: -2;\r\n            transform-origin: bottom right;\r\n            transition: transform .25s linear;\r\n        }\r\n    }\r\n}\r\n\r\n.center{\r\n    width: 100%;\r\n    height: 100%;\r\n    display: grid;\r\n    place-items: center;\r\n    .score{\r\n        text-align: center;\r\n        background: white;\r\n        padding: 5%;\r\n        border-radius: 10px;\r\n        h2{\r\n            font-size: 2rem;\r\n            margin: 20px;   \r\n        }\r\n        h3{\r\n            color: $dk-color;\r\n            font-size: 1.5rem;\r\n            margin: 20px;\r\n            display: inline-block;\r\n            padding: 20px 40px;\r\n            background: $pm-color;\r\n            border-radius: 10px;\r\n            box-shadow: 0px 5px 14px #dfdbdb;\r\n        }\r\n        p{\r\n            font-size: 1.2rem;\r\n            margin: 20px;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n.loader {\r\n    width: 48px;\r\n    height: 48px;\r\n    border: 5px solid $dk-color;\r\n    border-bottom-color: transparent;\r\n    border-radius: 50%;\r\n    display: inline-block;\r\n    box-sizing: border-box;\r\n    animation: rotation 1s linear infinite;\r\n    }\r\n\r\n    @keyframes rotation {\r\n    0% {\r\n        transform: rotate(0deg);\r\n    }\r\n    100% {\r\n        transform: rotate(360deg);\r\n    }\r\n}\r\n\r\n@media screen and (max-width:770px) {\r\n    \r\n    .container{\r\n        .home{\r\n            .heading{\r\n                font-size: 30px;\r\n            }\r\n            .cat-rule{\r\n                display: block;\r\n            }       \r\n       }\r\n    }    \r\n}\r\n"],"sourceRoot":""}]);
